import React, { Component } from "react";
import axios from "axios";
import Lottie from "react-lottie";
import { Form, Button } from "react-bootstrap";
import loader from "../../assets/animation/loader";
// import Layout from "../layout/layout";
import toast from "react-hot-toast";
// import logo from "../../assets/images/register/logocoin.png";
import "./register.css";

const loaderIcon = {
  loop: true,
  autoplay: true,
  animationData: loader,
};
export class FormOTP extends Component {
  constructor(props) {
    super(props);

    this.state = {
      phone: this.props.data.phone,
      otp: "",
      message: "",
      loading: false,
    };

    this.handleOTPChange = this.handleOTPChange.bind(this);
  }

  handleOTPChange(event) {
    this.setState({ otp: event.target.value });
  }

  formOTP(event) {
    event.preventDefault();
    if (this.state.otp === "")
      return this.setState({ message: "Field is required." });
    this.setState({ loading: true });
    axios
      .get("https://init-reg-t3e66zpola-ez.a.run.app", {
        params: {
          service: "veriOTC",
          phone: this.state.phone,
          smsOtc: this.state.otp,
        },
      })
      .then((response) => {
        this.setState({ loading: false });
        if (response.data) {
          const ErrorVariable = response.data.error;
          if (ErrorVariable === "wrong OTC") {
            toast.success("The entered code is incorrect", {
              id: "error-login",
              duration: 4000,
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });
            this.setState({
              message: "Code is incorrect, please try again.",
            });
          }
          if (response.data.error === "no error") {
            toast.success("Please create your account", {
              id: "account-Success",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });
            this.setState({
              message: "Success",
            });
            this.props.nextStep();
          } else {
            toast.success(ErrorVariable, {
              id: "backend-error",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });
          }
        }
      })
      .catch((error) => {
        this.setState({ loading: false });
        console.log("Error fetching and parsing data", error);
      });
  }

  render() {
    return (
      <div className="profile signup otp-form">
        <h1>OTP</h1>
        <p>Code sent via SMS, Please fill your OTP to create your account</p>
        <div className="rigister-tab">
          <Form>
            <div className="step-2">
                  <Form.Group controlId="formOTP">
                    <Form.Label>Enter OTP</Form.Label>
                    <Form.Control
                      type="number"
                      placeholder="Enter your OTP"
                      name="otp"
                      autoComplete="off"
                      value={this.state.value}
                      onChange={this.handleOTPChange}
                    />
                    <p style={{ color: "#b9a11e" }}>{this.state.message}</p>
                  </Form.Group>
                  <div className="text-center">
                    <Button
                      className="s-btn"
                      type="submit"
                      disabled={this.state.loading}
                      onClick={this.formOTP.bind(this)}
                    >
                      {this.state.loading ? (
                        <Lottie options={loaderIcon} height={30} width={30} />
                      ) : (
                        "Next"
                      )}
                    </Button>
                  </div>
            </div>
          </Form>
        </div>
      </div>
    );
  }
}

export default FormOTP;
