import React from "react";
import "./report.css";

const BlockUserPopUp = ({ nickname, handleBlock, handleClose }) => {
  return (
    <div className="report-user-popup">
      <div className="report-popup">
        <div className="report-popup-header">
          <h2>Block @{nickname.toUpperCase()}</h2>
        </div>

        <div className="block-popup-content">
          <p>
            Blocking <b>@{nickname.toUpperCase()}</b> on Las-Vegas will
            completely blind that person within your profile and theirs, they
            will not come up in your network.
          </p>
          <div className="action-block">
            <button
              className="btn-dark btn btn-primary"
              onClick={() => handleClose()}
            >
              Cancel
            </button>
            <button
              style={{ height: "35px" }}
              className="btn-gold btn btn-primary"
              onClick={handleBlock}
            >
              Confirm
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default BlockUserPopUp;
