import axios from "axios";
import firebase from "./firebase";

export const getDoc = async (u) => {
  //u = user.uid
  const res = await axios.get("https://get-doc-t3e66zpola-uc.a.run.app/", {
    params: {
      usid: u,
    },
  });
  if(res.data.success){
  return {
    ...res.data.doc,
	userDoc: res.data.userDoc,
    inGame: res.data.inGame,
    inQue: res.data.inQue,
    nextSpinLuckyWheel: res.data.nextSpinLuckyWheel,
	exchangeRate: res.data.exchangeRate,
	currency: res.data.currency,
	datetimeNow: res.data.datetimeNow
  };
}else if(u) {
	const db = firebase.firestore();
	const querySnapshot = await db
	  .collection("users")
	  .where("nickname", "==", u)
	  .get();
	 return await getDoc(querySnapshot.docs[0].id)
}
};


export const chatHistory = async (u, p) => {
  //u=uid p=peerId
  var x = [];
  var y = [];
  const db = firebase.firestore();
  const snapshot = await db
    .collection("messanger")
    .doc(u)
    .collection("chats")
    .where("from.uid", "in", [u, p])
    .where("to", "==", p)
    .orderBy("date", "asc")
    .limitToLast(500)
    .get();
  snapshot.forEach(async (doc) => {
    x.push(doc.data());
    await db
      .collection("messanger")
      .doc(u)
      .collection("chats")
      .doc(doc.id)
      .update({
        isRead: true,
      });
  });

  const snapshot1 = await db
    .collection("messanger")
    .doc(u)
    .collection("chats")
    .where("from.uid", "in", [u, p])
    .where("to", "==", u)
    .orderBy("date", "asc")
    .limitToLast(500)
    .get();
  snapshot1.forEach(async (doc) => {
    x.push(doc.data());
    await db
      .collection("messanger")
      .doc(u)
      .collection("chats")
      .doc(doc.id)
      .update({
        isRead: true,
      });
  });
  const chat = x.concat(y);
  chat.sort((a, b) => a.date.toDate() - b.date.toDate());
  return chat;
};

