export const getFriendsOfuser = async (u) => {
  //u = user.uid
  var res = [];
  var myUid;
  var myNick;
  const db = firebase.firestore();
  const querySnapshot = await db
    .collection("users")
    .doc(u)
    .collection("items")
    .where("type", "==", "friend")
    .get();
  querySnapshot.forEach(async (doc) => {
    myUid = doc.get("id");
    const nick = await db.collection("users").doc(myUid).get();
    myNick = nick.data().nickname;
    res.push({ value: myNick, label: myNick, id: doc.get("id") });
  });
  return res;
};

export const getFriendsPicker = async (u) => {
  //u = user.uid
  var uids = [];
  const db = firebase.firestore();
  const querySnapshot = await db
    .collection("users")
    .doc(u)
    .collection("items")
    .get();
  querySnapshot.forEach((doc) => {
    uids.push(doc.get("id"));
  });

  const res = uids.map(async (uid) => {
    const nick = await db.collection("users").doc(uid).get();
    const n = nick.data().nickname;
    return { value: n, label: n, id: uid };
  });
  return res;
};

export const getPeopleUKnow = async (uid) => {
  try {
    const db = firebase.firestore();
    // get friends
    let friend = [];
    let myfriend = [];
    let list = [];
    //const all_relations = ["friend", "blocked friend", "follower", "following", "removed follower"];
    let item = await db
      .collection("users")
      .doc(uid)
      .collection("items")
      .where("type", "==", "friend")
      .get();
    item.forEach(async (doc) => {
      myfriend.push({
        uid: doc.id,
      });
    });
    list = [...myfriend];
    if (myfriend.length > 4) {
      const shuffled = myfriend.sort(() => 0.5 - Math.random());

      // Get sub-array of first n elements after shuffled
      myfriend = shuffled.slice(0, 8);
      for (const fr of myfriend) {
        let hisfr = [];
        let hisFriend = await db
          .collection("users")
          .doc(fr.uid)
          .collection("items")
          .where("type", "==", "friend")
          .get();
        hisFriend.forEach((doc) => {
          let d = doc.data();
          hisfr.push({
            uid: doc.id,
            name: d.nickname,
            frRelated: getRelatedFriendCount(doc.id, list, uid),
            image: d.photoURL,
          });
        });
        for (const his of hisfr) {
          if (
            !list.find(
              (ele) =>
                ele.uid === his.uid &&
                his.uid !== uid &&
                his.uid !== "777-our-VEGAS-BOT-uid-777"
            )
          ) {
            friend.push(his);
          }
        }
      }
    } else if (myfriend.length > 0 && myfriend.length <= 4) {
      myfriend.forEach(async (my) => {
        const hisFriend = await db
          .collection("users")
          .doc(my.uid)
          .collection("items")
          .where("type", "==", "friend")
          .get();
        hisFriend.forEach(async (his) => {
          if (
            !list.find(
              (ele) =>
                ele.uid === his.id &&
                his.id !== uid &&
                his.id !== "777-our-VEGAS-BOT-uid-777"
            )
          ) {
            let data = his.data();
            friend.push({
              uid: his.id,
              name: data.nickname,
              frRelated: await getRelatedFriendCount(his.id, myfriend, uid),
              image: data.avatar,
            });
          }
        });
      });
    } else {
      const user = await db.collection("users").get();
      user.forEach((item) => {
        if (item.id !== uid && item.id !== "777-our-VEGAS-BOT-uid-777") {
          let data = item.data();
          friend.push({
            uid: item.id,
            name: data.nickname,
            frRelated: 0,
            image: data.avatar,
          });
        }
      });
    }
    const shuffled = friend.sort(() => 0.5 - Math.random());
    friend = shuffled.slice(0, 8);
    return friend;
  } catch (err) {
    return [];
  }
};

export const getRelatedFriendCount = async (he, myfr, myId) => {
  const db = firebase.firestore();
  const hisFriend = await db
    .collection("users")
    .doc(he)
    .collection("items")
    .where("type", "==", "friend")
    .get();
  let count = 0;
  hisFriend.forEach((item) => {
    if (
      item.id !== "777-our-VEGAS-BOT-uid-777" &&
      item.id !== myId &&
      myfr.find((ele) => ele.uid === item.id)
    ) {
      count = count + 1;
    }
  });
  return 0;
};

export const getPurchasedItem = async (myId) => {
  try {
    let arr = [];
    const db = firebase.firestore();
    const items = await db
      .collection("users")
      .doc(myId)
      .collection("items")
      .where("type", "==", "avatar-frame")
      .get();
    items.forEach((item) => {
      arr.push(item.data());
    });
    return arr;
  } catch (error) {
    console.log("Error in getPurchased Item =>", error.message);
  }
};

// time calculation
export const timeDifference = (time1) => {
  const current = new Date();
  const previous = new Date(time1);
  let msPerMinute = 60 * 1000;
  let msPerHour = msPerMinute * 60;
  let msPerDay = msPerHour * 24;
  let msPerMonth = msPerDay * 30;
  let msPerYear = msPerDay * 365;

  let elapsed = current - previous;

  if (elapsed < msPerMinute) {
    return Math.round(elapsed / 1000) + " seconds ago";
  } else if (elapsed < msPerHour) {
    return Math.round(elapsed / msPerMinute) + " minutes ago";
  } else if (elapsed < msPerDay) {
    return Math.round(elapsed / msPerHour) + " hours ago";
  } else if (elapsed < msPerMonth) {
    return Math.round(elapsed / msPerDay) + " days ago";
  } else if (elapsed < msPerYear) {
    return Math.round(elapsed / msPerMonth) + " months ago";
  } else {
    return Math.round(elapsed / msPerYear) + " years ago";
  }
};

