import React, { useEffect, useContext } from "react";
import { Link, useHistory } from "react-router-dom";
import { Navbar, Button, Modal, Spinner, Dropdown } from "react-bootstrap";
import "./header.css";
import axios from "axios";
import firebase from "../../firebase";
import { useState } from "react";
import { MainContext } from "../../context";
import Bell from "../bell";
import logo from "../../assets/images/header/logo-new.svg";
import india from "../../assets/images/header/in.svg";
import us from "../../assets/images/header/us.svg";
import helpicon from "../../assets/images/register/help.png";
import { useTranslation } from "react-i18next";

const Header = () => {
  const context = useContext(MainContext);
  const [uid, setUid] = useState();
  const [token, setToken] = useState();
  const [notification, setNotification] = useState([]);
  const [unreadCount, setunreadCount] = useState();
  const history = useHistory();
  const [usid] = useState(localStorage.getItem("uid"));
  const [gameLink, setGameLink] = useState("");
  const [leaveTableUrl, setLeaveLink] = useState("");
  const [showInGamePopup, setShowInGamePopup] = useState(false);
  const [loader, setLoader] = useState(false);
  const [stickyHeader, setStickyHeader] = useState(false);
  const [flag, setFlage] = useState("hn");

  const { t, i18n } = useTranslation();

  const changeLanguage = (lng) => {
    setFlage(lng);
    i18n.changeLanguage(lng);
  };

  const watchScroll = () => {
    if (window.scrollY > 0) {
      setStickyHeader(true);
    } else {
      setStickyHeader(false);
    }
  };
  useEffect(() => {
    changeLanguage(flag);
  }, []);

  useEffect(() => {
    if (window !== undefined && window.screen.width >= 0) {
      window.addEventListener("scroll", watchScroll);
    }
    return () => {
      window.removeEventListener("scroll", watchScroll);
    };
  }, []);

  const getNotification = async (token) => {
    const res = await axios.get("https://fetch-noti-t3e66zpola-uc.a.run.app/", {
      headers: {
        idToken: token,
      },
    });
    if (res.data.success) {
      context.setNotification(res.data.notiArray);
      const count = res.data.notiArray.reduce((prev, curr) => {
        return prev + !curr.isRead;
      }, 0);
      setNotification(res.data.notiArray);
      setunreadCount(count);
    }
  };

  useEffect(() => {
    const checkIsLoggedIn = async () => {
      firebase.auth().onAuthStateChanged(async (user) => {
        if (user) {
          const us = firebase.auth().currentUser;
          const token = await firebase.auth().currentUser.getIdToken(true);
          setToken(token);
          setUid(us.uid);
          getNotification(token);
          const res = await axios.get(
            "https://check-device-id-t3e66zpola-ez.a.run.app/",
            {
              headers: {
                idtoken: token,
                device: localStorage.getItem("deviceId"),
              },
            }
          );
          if (res.data) {
            if (res.data.error === "user logged in other device")
              doSignout(us.uid, token);
          }
        }
      });
    };
    checkIsLoggedIn();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const checkInGame = async () => {
      const backgammonCheck = await axios.get(
        `https://backgammon-server01-t3e66zpola-uc.a.run.app/checkUserInGame/${usid}`
      );
      if (backgammonCheck.data.gameStatus === "InGame") {
        setShowInGamePopup(true);
        setLeaveLink(backgammonCheck.data.leaveTableUrl);
        setGameLink(backgammonCheck.data.link);
        setTimeout(() => {
          window.location.reload();
        }, 180000);
      }
      const pokerCheck = await axios.get(
        `https://poker-server-t3e66zpola-uc.a.run.app/checkUserInGame/${usid}`
      );
      if (pokerCheck.data.gameStatus === "InGame") {
        setShowInGamePopup(true);
        setLeaveLink(pokerCheck.data.leaveTableUrl);
        setGameLink(pokerCheck.data.link);
        setTimeout(() => {
          window.location.reload();
        }, 180000);
      }
      const blackJackCheck = await axios.get(
        `https://blackjack-server-t3e66zpola-uc.a.run.app/checkUserInGame/${usid}`
      );
      if (blackJackCheck.data.gameStatus === "InGame") {
        setShowInGamePopup(true);
        setLeaveLink(blackJackCheck.data.leaveTableUrl);
        setGameLink(blackJackCheck.data.link);
        setTimeout(() => {
          window.location.reload();
        }, 180000);
      }

      const ludoCheck = await axios.get(
        `https://ludo-server-t3e66zpola-uc.a.run.app/checkUserInGame/${usid}`
      );
      console.log(ludoCheck, "fghfhfg");
      if (ludoCheck.data.gameStatus === "InGame") {
        setShowInGamePopup(true);
        setLeaveLink(ludoCheck.data.leaveTableUrl);
        setGameLink(ludoCheck.data.link);
        setTimeout(() => {
          window.location.reload();
        }, 180000);
      }
    };
    if (usid) {
      checkInGame();
    }
  }, [usid]);

  const handleLeave = async () => {
    setLoader(true);
    const res = await axios.get(leaveTableUrl);
    if (res.status === 200) {
      window.location.reload();
    }
  };

  useEffect(() => {
    const getFeedListener = async () => {
      const db = firebase.firestore();
      if (!uid) return;
      db.collection("messanger")
        .doc(uid)
        .collection("noti")
        .onSnapshot(async (snapshot) => {
          if (snapshot.docs.length < 1) {
            return 0;
          }
          getNotification(token);
        });
    };
    getFeedListener();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [uid, token]);

  const doSignout = (uid, token) => {
    axios
      .get("https://base-api-t3e66zpola-uk.a.run.app/", {
        params: {
          usid: uid,
          service: "signout",
          params: `usid=${uid}`,
        },
        headers: { idtoken: token },
      })
      .then((response) => {
        if (response.data.error === "no error") {
          firebase
            .auth()
            .signOut()
            .then(() => {
              window.location.href = "/login";
            })
            .catch((error) => {
              console.log(error);
            });
        } else {
          console.log("req failed: ", response.data);
        }
      })
      .catch((error) => {
        console.log("Error req", error);
      });
  };

  const pathname = window.location.pathname;
  const splitLocation = pathname.split("/");
  const xtkn = localStorage.getItem("xtkn");

  if (pathname === "/login") {
    return (
      <div
        className={`header help-center-header ${
          stickyHeader ? "sticky-header" : ""
        }`}
      >
        <div className='header-top-area'>
          <div className='container'>
            <div className='header-wrapper'>
              <div className='logo'>
                <a href='https://las-vegas.com/profile'>
                  <img src={logo} alt='logo' />
                </a>
              </div>

              <div className='auth-btns'>
                <a href='https://las-vegas.com/login' className='login-btn'>
                  {t("landing.login")}
                </a>
                <Link to='/' className='signup-btn'>
                  Register
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className='help-desk-icon'>
          <span
            onClick={() => {
              history.push("/help-center");
            }}
            role='presentation'
          >
            <img src={helpicon} alt='help' />
          </span>
        </div>
      </div>
    );
  }

  if (pathname === "/reset_password") {
    return (
      <div className={`header ${stickyHeader ? "sticky-header" : ""}`}></div>
    );
  }

  if (pathname === "/") {
    return (
      <div
        className={`header help-center-header ${
          stickyHeader ? "sticky-header" : ""
        }`}
      >
        <div className='header-top-area'>
          <div className='container'>
            <div className='header-wrapper'>
              <div className='logo'>
                <a href='https://las-vegas.com/profile'>
                  <img src={logo} alt='logo' />
                </a>
              </div>
              <div className='header-wrapper-right'>
                <div className='auth-btns'>
                  <a href='https://las-vegas.com/login' className='login-btn'>
                    {t("landing.login")}
                  </a>

                  <a href='https://las-vegas.com' className='signup-btn'>
                    {t("landing.register")}
                  </a>
                </div>
                <div className='language-dropdown'>
                  <Dropdown>
                    <Dropdown.Toggle id='dropdown-basic'>
                      {flag === "hn" ? (
                        <img src={india} alt='flag' />
                      ) : (
                        <img src={us} alt='flag' />
                      )}
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                      <Dropdown.Item onClick={() => changeLanguage("hn")}>
                        <img src={india} alt='flag' />
                        
                      </Dropdown.Item>
                      <Dropdown.Item onClick={() => changeLanguage("en")}>
                        <img src={us} alt='flag' />
                        English
                      </Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (xtkn === null && pathname === "/help-center") {
    return (
      <div
        className={`header help-center-header ${
          stickyHeader ? "sticky-header" : ""
        }`}
      >
        <div className='header-top-area'>
          <div className='container'>
            <div className='header-wrapper'>
              <div className='logo'>
                <Link to='/profile'>
                  <img src={logo} alt='logo' />
                </Link>
              </div>

              <div className='auth-btns'>
                <a href='https://las-vegas.com/login' className='login-btn'>
                  {t("landing.login")}
                </a>
                <Link to='/' className='signup-btn'>
                  Register
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className='help-desk-icon'>
          <span
            onClick={() => {
              history.push("/help-center");
            }}
            role='presentation'
          >
            <img src={helpicon} alt='help' />
          </span>
        </div>
      </div>
    );
  }

  const getBellIconAndLogin = () => {
    if (pathname === "/profile") {
      return (
        <Bell
          uid={uid}
          idToken={token}
          notification={notification}
          unreadCount={unreadCount}
        />
      );
    } else if (pathname === "/" || pathname === "/login") {
      return (
        <>
          <div className='auth-btns'>
            <a href='https://las-vegas.com/login' className='login-btn'>
              {t("landing.login")}
            </a>
            <Link to='/' className='signup-btn'>
              Register
            </Link>
          </div>
        </>
      );
    }
    return "";
  };

  return (
    <div className={`header ${stickyHeader ? "sticky-header" : ""}`}>
      <div className='container'>
        <Navbar
          collapseOnSelect
          expand='lg'
          variant='dark'
          style={{ color: "#191b25" }}
        >
          <Navbar.Toggle aria-controls='responsive-navbar-nav' />
          <Navbar.Collapse id='responsive-navbar-nav'>
            {" "}
            <div className='logo'>
              <Link to='/profile'>
                <img src={logo} alt='logo' />
              </Link>
            </div>{" "}
            <div className='header-top-login after-login'>
              <ul>
                <li className=' wow animate__animated animate__slideInUp'>
                  <span
                    className={
                      splitLocation[1] === "profile" ? "activeheader" : "false"
                    }
                    role='presentation'
                  >
                    <Link to='/profile'>
                      <i className='las la-home'></i>
                      <p>GAMES</p>
                    </Link>
                  </span>
                </li>
                <li className=' wow animate__animated animate__slideInUp'>
                  <span
                    className={
                      splitLocation[1] === "friends" ? "activeheader" : "false"
                    }
                    role='presentation'
                  >
                    <Link to='/friends'>
                      <i className='las la-user-friends'></i>
                      <p>Friends</p>
                    </Link>
                  </span>
                </li>
                <li className=' wow animate__animated animate__slideInUp'>
                  <span
                    className={
                      splitLocation[1] === "feed" ? "activeheader" : "false"
                    }
                    role='presentation'
                  >
                    <Link to='/feed'>
                      <i className='las la-comments'></i>
                      <p>FEED</p>
                    </Link>
                  </span>
                </li>
                {(pathname !== "/" || pathname !== "/login") && (
                  <li className=' wow animate__animated animate__slideInUp'>
                    <span
                      className={
                        splitLocation[1] === "settings"
                          ? "activeheader"
                          : "false"
                      }
                      role='presentation'
                    >
                      <Link to='/settings'>
                        <i className='las la-cog'></i>
                        <p>Settings</p>
                      </Link>
                    </span>
                  </li>
                )}
              </ul>
            </div>
            <div className='bell-section'>{getBellIconAndLogin()}</div>
          </Navbar.Collapse>
        </Navbar>
        {/*<div className="header-wrapper main-header-wrapper">
          <Link to="/">
            {" "}
            <div className="logo">
              <img src={logo} alt="logo" />
            </div>{" "}
          </Link>

          <div className="auth-btns">
            <Link to="/login" className="login-btn">
              Login
            </Link>
            <Link to="/" className="signup-btn">
              Register
            </Link>
          </div>
        </div>*/}
      </div>
      {/*<div className="show-mob">
        <Bell
          uid={uid}
          idToken={token}
          notification={notification}
          unreadCount={unreadCount}
        />
      </div>*/}
      {pathname === "/settings" ? (
        <div className='help-desk-icon'>
          <span
            onClick={() => {
              history.push("/help-center");
            }}
            role='presentation'
          >
            <img src={helpicon} alt='help' />
          </span>
        </div>
      ) : (
        ""
      )}
      <Modal
        show={showInGamePopup}
        centered
        className='friends-popup in-game'
        size='lg'
      >
        <Modal.Body>
          <div className='in-game-popup'>
            <h6>You have an active game</h6>
            <div className='action'>
              <Button
                className='leave-game'
                onClick={handleLeave}
                disabled={loader}
              >
                {loader ? <Spinner animation='border' /> : "Leave Game"}
              </Button>
              <Button
                className='btn-gold rejoin-class'
                onClick={() => (window.location.href = gameLink)}
                disabled={loader}
              >
                Rejoin Game
              </Button>
            </div>
          </div>
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default Header;
