import React, { Component } from "react";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import axios from "axios";
import toast from "react-hot-toast";
import { Form, Button } from "react-bootstrap";
import {  isMobile, browserName } from "react-device-detect";
import "./register.css";
//import lvVideo from "../../assets/video/LAS_VEGAS.mp4";
import CircularTimer from "./circularTimer";
import GoogleLogin from "react-google-login";

export class FormPhone extends Component {
  constructor(props) {
    super(props);
    this.state = {
      country: "US",
      city: "",
      ip: "",
      phone: this.props.data.phone,
      device: "",
      error: "",
      showVideoPopup: !isMobile,
      otp: "",
      xtkn: localStorage.getItem("xtkn"),
      loading: false,
      accessRestricted: false,
      deviceId: "",
      RegisterShow: false,
      showVideo: true,
      videoType: "poker",
      NFTPopup: false,
      statShow: false,
    };
    this.handleGoogleLogin = this.handleGoogleLogin.bind(this);
  }

  async geolocationSuccess(position) {
    const {
      coords: { latitude, longitude },
    } = position;
    console.log("Dsdsd =>", position);
    const res = await axios.get(
      `http://api.geonames.org/countryCodeJSON?lat=${latitude}&lng=${longitude}&username=rizwanwebsul`
    );
    console.log("res =>", res.data);
    this.setState({ countryCode: res.data.countryCode });
    this.props.handleUserOrigin(this.state.ip, res.data.countryCode);
  }

  async componentDidMount() {
    // Geolocation IP Databases allow  to determine your app visitor's location: https://geolocation-db.com/
    let countryCode;
    const url =
      "https://geolocation-db.com/json/0f761a30-fe14-11e9-b59f-e53803842572";
    const response = await axios.get(url);
    let { data } = response;
    if (data.country_code === "Not found" || !data?.country_code) {
      const res = await axios.get("https://www.cloudflare.com/cdn-cgi/trace");
      const values = res.data.split("\n");
      let obj = {};
      values.forEach((value) => {
        obj[value.split("=")[0]] = value.split("=")[1];
      });
      data = { IPv4: obj.ip, country_code: obj.loc };
    }
    this.setState({
      ip: data.IPv4,
      city: data.city,
    });
    this.props.handleUserOrigin(data.IPv4, data.country_code);
    countryCode = data.country_code;

    // SetUp phone Country code flag according to Geolocation data.country_name IP
    if (
      countryCode === "International" ||
      countryCode === "Not found" ||
      countryCode === "" ||
      !countryCode
    ) {
      if (window.navigator) {
        window.navigator.geolocation.getCurrentPosition(
          async (position) => {
            countryCode = await this.geolocationSuccess(position);
          },
          (err) => {
            console.log("error in geolocation", err);
          },
          {
            enableHighAccuracy: true,
            timeout: 5000,
            maximumAge: 0,
          }
        );
      }
    } else {
      this.setState({
        accessRestricted: false,
      });
    }
    this.setState({
      country: countryCode,
    });
  }

  ifRegistr = () => {
    let validError = "Auto sign out, please try again";
    let checkCookies = this.state.xtkn;
    if (checkCookies) {
      this.setState({ error: validError });
      localStorage.removeItem("xtkn");
      toast.success("Auto sign out, please try again", {
        id: "currently-signed-in",
        icon: "",
        style: {
          borderRadius: "5px",
          background: "#333",
          color: "#fff",
        },
      });
      return false;
    }
    return true;
  };

  formPhoneNumber(event) {
    event.preventDefault();
    this.props.phoneUpdate(this.state.phone);
    if (this.state.phone === "")
      return this.setState({
        error: "Phone number is required",
      });

    if (this.state.phone === undefined)
      return this.setState({
        error: "Phone number is undefined",
      });

    this.setState({ loading: true });

    var browser = browserName;
    this.device = browser;

    const isValid = this.ifRegistr();

    if (isValid) {
      window.gtag("event", "Phone_Verification", {
        title: "Google registration button event",
      });
      toast.success("Sending OTP via SMS..", {
        id: "OTP-SMS",
        duration: 4000,
        icon: "",
        style: {
          borderRadius: "5px",
          background: "#333",
          color: "#fff",
        },
      });
      localStorage.removeItem("welcomePopup");
      axios
        .get("https://init-reg-t3e66zpola-ez.a.run.app", {
          params: {
            phone: this.state.phone,
            ip: this.state.ip,
            device: this.device,
            db: "yes",
            sms: "yes",
          },
          headers: {
            fmctoken: localStorage.getItem("fmcToken")
              ? localStorage.getItem("fmcToken")
              : "no",
          },
        })
        .then((response) => {
          if (response.data) {
            this.setState({ loading: false });
            const myErrorVariable = response.data.error;
            const myError2 = response.data.error2;
            const OTC = response.data.OTC;
            const duplicate = response.data.phone_in_DB;

            if (OTC) {
              console.log("OTP: " + OTC);
              toast.success("SMS Successfully sent", {
                id: "otp-Success",
                duration: 4000,
                icon: "",
                style: {
                  borderRadius: "5px",
                  background: "#333",
                  color: "#fff",
                },
              });

              this.setState({
                otp: OTC,
              });

              this.props.phoneUpdate(this.state.phone);
              this.props.nextStep();
            }

            if (
              duplicate === true &&
              myError2 === "phone belongs to registered user"
            ) {
              toast.success("Phone already registered", {
                id: "error-duplicate",
                duration: 4000,
                icon: "",
                style: {
                  borderRadius: "5px",
                  background: "#333",
                  color: "#fff",
                },
              });
              this.setState({
                error: "Phone already registered, please login",
              });
            }
            if (duplicate === true && myError2 === "no registerDate found") {
              this.props.nextStep();
            } else if (myErrorVariable === "number is not valid") {
              toast.success("Phone number is invalid", {
                id: "error-register",
                duration: 4000,
                icon: "",
                style: {
                  borderRadius: "5px",
                  background: "#333",
                  color: "#fff",
                },
              });
              this.setState({ error: "Phone number is invalid" });
            } else if (myErrorVariable === "phone pre check failed") {
              toast.success("You've made too many attempts", {
                id: "error-register",
                duration: 4000,
                icon: "",
                style: {
                  borderRadius: "5px",
                  background: "#333",
                  color: "#fff",
                },
              });
              this.setState({ error: "Too many attempts, try again later" });
            } else if (myErrorVariable === "no error") {
              this.setState({ error: myErrorVariable });
            } else if (myErrorVariable !== "no error") {
              toast.success(myErrorVariable, {
                id: "backend-error",
                icon: "",
                style: {
                  borderRadius: "5px",
                  background: "#333",
                  color: "#fff",
                },
              });
            }
          }
        })
        .catch((error) => {
          this.setState({ loading: false });
          console.log("Error fetching and parsing data", error);
        });
    }
  }

  handleVideoPopup = (value) => {
    this.setState({ showVideoPopup: value });
  };

  toggleState() {
    this.setState({
      statShow: !this.state.statShow,
    });
  }

  async handleGoogleLogin(response) {
    console.log("error =>", response.error);
    if (response.error) return;
    const {
      profileObj: { email },
      tokenObj: { access_token },
    } = response;
    this.props.handleGoogleLogin(email, access_token, this.state.country);
  }

  RegisterClose() {
    this.setState({
      RegisterShow: !this.state.RegisterShow,
    });
  }

  handleRgister(type) {
    this.setState({
      RegisterShow: true,
      videoType: type,
      showVideo: true,
    });
    setTimeout(() => {
      this.setState({ showVideo: false });
    }, 5000);
  }

  handleNFTPopup = () => {
    this.setState({ NFTPopup: true });
  };
  handleNFTPopupClose = () => {
    this.setState({ NFTPopup: false });
  };
  handleRegisterPopup = () => {
    this.setState({ showVideo: false });
    this.setState({ RegisterShow: true });
  };
  render() {
    return (
      <>
        <div className="profile register-page signup-step-one">
          <div className="google-login">
            <LoginButton handleGoogleLogin={this.handleGoogleLogin} />
            <div className="divider">
              <span>OR</span>
            </div>

            <button
              className={
                this.state.statShow
                  ? "google-login-btn btn btn-primary"
                  : "google-login-btn btn btn-primary"
              }
              onClick={() => this.toggleState()}
            >
              <b>Phone</b> Verification
            </button>
          </div>

          {this.state.statShow ? (
            <Form>
              <div className="step-1">
                <div className="row">
                  <div className="col-md-12">
                    <Form.Group controlId="formPhoneNumber">
                      <Form.Label>Enter Phone Number</Form.Label>
                      <PhoneInput
                        className="PhoneInput"
                        placeholder="Enter phone number"
                        value={this.state.phone}
                        onChange={(phone) => this.setState({ phone })}
                        defaultCountry={this.state.country}
                        required
                      />
                      <p style={{ color: "#b9a11e" }}>{this.state.error}</p>
                      <p style={{ color: "#b9a11e" }}>{this.state.otp}</p>
                    </Form.Group>
                  </div>
                  <div className="col-md-12">
                    <div className="text-center">
                      <Button
                        className="s-btn"
                        type="submit"
                        disabled={this.state.loading}
                        onClick={this.formPhoneNumber.bind(this)}
                      >
                        {this.state.loading ? (
                          <CircularTimer />
                        ) : (
                          "Send SMS Code"
                        )}
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </Form>
          ) : null}

          {/*{isIOS && isMobile && (
                  <div className="apple-store">
                    <a href="https://apps.apple.com/app/las-vegas-com/id1605959990?platform=iphone">
                      <img className="img-responsive" src={IOS} alt="ios" />
                    </a>
                  </div>
                )}

                {!isIOS && isMobile && (
                  <div className="google-play">
                    <a href="https://play.google.com/store/apps/details?id=com.las_vegas.twa&pcampaignid=pcampaignidMKT-Other-global-all-co-prtnr-py-PartBadge-Mar2515-1">
                      <img
                        className="img-responsive"
                        src={googlePlay}
                        alt="google"
                      />
                    </a>
                  </div>
                )}

                {!isMobile && (
                  <div>
                    <div className="google-play">
                      <a href="https://play.google.com/store/apps/details?id=com.las_vegas.twa&pcampaignid=pcampaignidMKT-Other-global-all-co-prtnr-py-PartBadge-Mar2515-1">
                        <img
                          className="img-responsive"
                          src={googlePlay}
                          alt="google"
                        />
                      </a>
                    </div>
                    <div className="apple-store">
                      <a href="https://apps.apple.com/app/las-vegas-com/id1605959990?platform=iphone">
                        <img className="img-responsive" src={IOS} alt="ios" />
                      </a>
                    </div>
                  </div>
                )}*/}
        </div>
      </>
    );
  }
}

export default FormPhone;

const LoginButton = ({ handleGoogleLogin }) => {
  window.gtag("event", "Google_registration", {
    title: "Google registration button event",
  });

  return (
    <div className="login-button" id="buttonDiv">
      <GoogleLogin
        clientId="139027047038-dq2sqld3ba2fa5fn8ck0ptf335e7kvb6.apps.googleusercontent.com"
        render={(renderProps) => (
          <Button
            onClick={renderProps.onClick}
            disabled={renderProps.disabled}
            className="google-login-btn"
          >
            <i className="fa fa-google" aria-hidden="true"></i> Sign up with
            <b> Google</b>
          </Button>
        )}
        buttonText="Login"
        onSuccess={handleGoogleLogin}
        onFailure={handleGoogleLogin}
        cookiePolicy={"single_host_origin"}
      />
    </div>
  );
};
