import React, { useEffect, useState, useRef, useContext } from "react";
import { Form, Button, Modal, Dropdown, DropdownButton } from "react-bootstrap";
//import { Link } from "react-router-dom";
import toast from "react-hot-toast";
import user1 from "../../assets/images/header/logo.png";
import "./chat.css";
import firebase from "../../firebase";
import { chatHistory } from "../../dbFetchFunctions";
import axios from "axios";
import { useHistory } from "react-router-dom";
import EmojiPicker from "./Emoji-picker";
import ReportUserPopup from "../reportpopup/reportuserpopup";
import BlockUserPopUp from "../reportpopup/blockUser";
import { MainContext } from "../../context";

const Chat = ({ open, handleClick }) => {
  const [usid] = useState(firebase.auth()?.currentUser?.uid);
  const [nick] = useState(firebase.auth()?.currentUser?.displayName);
  const [peer, setPeer] = useState("");
  // const [photoUri, setPhotoUri] = useState(["", ""]);
  const [show, setShow] = useState(true);
  const [chatHist, setChatHist] = useState([]);
  const [userPhoto, setuserPhoto] = useState("");
  const [peerPhoto, setPeerPhoto] = useState("");
  const [peername, setpeername] = useState("");
  const [idToken, setIdToken] = useState();
  useEffect(() => {
    const img = firebase.auth()?.currentUser?.photoURL;
    const hdd = firebase.storage();
    if (img) {
      const gsRef = hdd.refFromURL(img);
      gsRef.getDownloadURL().then((url) => setuserPhoto(url));
    }
  }, []);

  const [last_unread, setLast_unread] = useState({
    uPeers: [],
    last: [],
    unread: [],
    urCount: [],
  });

  useEffect(() => {
    const getChatPeers = async (u) => {
      const db = firebase.firestore();

      db.collection("messanger")
        .doc(u)
        .collection("chats")
        .where("to", "==", u)
        .orderBy("date", "desc")
        .onSnapshot(async (snapshot) => {
          if (snapshot.docs.length < 1) {
            return 0;
          }
          let newPeer = "";
          var uniqueChatPeers = [];
          var x = [];
          var y = [];
          var z = [];
          var maxX;
          var maxY;
          var c = 0;

          snapshot.forEach(async (doc) => {
            newPeer = doc.get("from.uid");
            if (uniqueChatPeers.find((ele) => ele.id === newPeer)) {
              if (doc.get("isRead") === false) {
                let le = z.length - 1;
                z[le] = z[le] + 1;
                if (doc.get("date").seconds > maxY) {
                  maxY = doc.get("date").seconds;
                  y[le] = doc.data();
                }
              } else {
                if (doc.get("date").seconds > maxX) {
                  maxX = doc.get("date").seconds;
                  let la = x.length - 1;
                  x[la] = doc.data();
                }
              }
            } else {
              c = 0;
              uniqueChatPeers.push({ id: newPeer });

              // last and unred messages:
              if (doc.get("isRead") === false) {
                c++;
                y.push(doc.data());
                maxY = doc.get("date").seconds;
                x.push(doc.data());
                maxX = maxY;
                z.push(c);
              } else {
                z.push(c);
                x.push(doc.data());
                maxX = doc.get("date").seconds;
              }
            }
          });
          if (y.length === 1 && peer === y[0].from.uid) {
            y = [];
            z = [];
          }
          let index = x.findIndex(
            (ele) => ele.from.uid === "777-our-VEGAS-BOT-uid-777"
          );
          let botCount = z.splice(index, 1);
          let bot = x.splice(index, 1);
          x.splice(0, 0, bot[0]);
          z.splice(0, 0, botCount);
          //  const blockedPeer = await db
          //    .collection("users")
          //    .doc(u)
          //    .collection("items")
          //    .where("type", "==", "blocked friend")
          //    .get();
          //  if (blockedPeer.docs.length > 0) {
          //    blockedPeer.forEach((blocked) => {
          //      let index = x.findIndex(
          //        (ele) => ele.from.uid === blocked.data().id
          //      );
          //      if (index !== -1) {
          //         z.splice(index, 1);
          //        x.splice(index, 1);
          //        y = y.filter(uniq => uniq.from.uid !== blocked.data().id)
          //      }
          //    });
          //  }
          setLast_unread({
            uPeers: uniqueChatPeers,
            last: x,
            unread: y,
            urCount: z,
          });
          let activePeers = [];
          for await (let doc of uniqueChatPeers) {
            let activeStatus = "offline";
            const sessions = await db
              .collection("users")
              .doc(doc.id)
              .collection("Sessions")
              .where("date.close", "==", null)
              .get();
            sessions.forEach(async (session) => {
              let a = session.data();
              if (a.agent.status === "online") {
                activeStatus = "online";
              } else if (a.agent.status === "offline") {
                activeStatus = "offline";
              } else {
                activeStatus = "inGame";
              }
            });
            activePeers.push({ id: doc.id, activeStatus });
          }
          setLast_unread({
            uPeers: activePeers,
            last: x,
            unread: y,
            urCount: z,
          });
        });
    };
	if(usid)
    getChatPeers(usid);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getChatHist = async (peerId, img, name) => {
    setPeer(peerId);
    setIdToken(await firebase.auth().currentUser.getIdToken(true));
    setShow(false);
    setPeerPhoto(img);
    setpeername(name);
    const msg = await chatHistory(usid, peerId);
    setChatHist(msg);
  };

  const handleBackClick = async () => {
    setChatHist([]);
    setPeer("");
    setPeerPhoto("");
    setShow(true);
    setpeername("");
  };

  // const imgUser = firebase.auth( ).currentUser.photoURL;

  const [view, setView] = useState(false);

  const handleClose = () => setView(false);
  const handleShow = (value) => setView(value);
  const handleBlock = () => {
    toast.success("Blocking..", {
      duration: 5000,
      id: "blocking-wait",
      icon: "",
      style: {
        borderRadius: "5px",
        background: "#333",
        color: "#fff",
      },
    });

    axios
      .get("https://base-api-t3e66zpola-uk.a.run.app", {
        params: {
          usid: peer,
          service: "blockUser",
          params: "usid=" + usid + ",block=" + peer + ",type=block",
        },
        headers: { idtoken: idToken },
      })
      .then((response) => {
        if (response.data) {
          const myError = response.data.error;

          if (myError === "no error") {
            toast.success("Your friend just been blocked!", {
              duration: 6000,
              id: "friend-blocked",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });

            handleClose();
          }
        } else {
          console.log("Backend response failed: ", response.statusText);
        }
      })
      .catch((error) => {
        console.log("Error req", error);
      });
  };
  if(!usid)
return ""

  return (
    <div className={`chat-wrapper ${!open ? `expand` : ``}`}>
      <div className="chat-section">
        <div className="chat-header">
          {last_unread.unread.length > 0 ? (
            <span className="unread-alert">{last_unread.unread.length}</span>
          ) : (
            ""
          )}
          <span className="chat-author">
            {peerPhoto === "" ? (
              <div className="report-post" onClick={() => handleClick(!open)}>
                <img
                  src={
                    peerPhoto !== ""
                      ? peerPhoto
                      : userPhoto !== ""
                      ? userPhoto
                      : user1
                  }
                  alt="user"
                />
                {peername !== "" ? peername : "Messaging"}
              </div>
            ) : (
              <div className="report-post">
                <DropdownButton
                  id="dropdown-basic-button"
                  title={
                    <>
                      <img
                        src={
                          peerPhoto !== ""
                            ? peerPhoto
                            : userPhoto !== ""
                            ? userPhoto
                            : user1
                        }
                        alt="user"
                      />
                      {peername !== "" ? peername : "Messaging"}
                    </>
                  }
                >
                  <Dropdown.Item onClick={() => handleShow("report")}>
                    <div className="reort-post-item">
                      <i
                        className="fa fa-exclamation-triangle"
                        aria-hidden="true"
                      ></i>
                      <div className="reort-post-item-content">
                        <h6>Report User</h6>
                        <p>I'm concerned about this user</p>
                      </div>
                    </div>
                  </Dropdown.Item>
                  <Dropdown.Item onClick={() => handleShow("block")}>
                    <div className="reort-post-item">
                      <i className="fa fa-ban" aria-hidden="true"></i>
                      <div className="reort-post-item-content">
                        <h6>Block</h6>
                        <p>I'm concerned about this user</p>
                      </div>
                    </div>
                  </Dropdown.Item>
                </DropdownButton>
              </div>
            )}
          </span>
          {/*<img src={userPhoto !== "" ? userPhoto : user1} alt="user" />
            Messaging
          </span>*/}
          {!show && (
            <div
              className="chat-back"
              onClick={() => handleBackClick()}
              role="presentation"
            >
              <i className="fa fa-angle-left" />
            </div>
          )}
          <span
            className="close-icon"
            onClick={() => handleClick(!open)}
            role="presentation"
          >
            <i className={`fa fa-${!open ? `angle-up` : `angle-down`}`} />
          </span>
        </div>
        {show ? (
          <ChatUserList
            setShow={setShow}
            setPeer={setPeer}
            last_unread={last_unread}
            getChatHist={getChatHist}
          />
        ) : (
          <SingleChatUser
            usid={usid}
            nick={nick}
            userPhoto={userPhoto}
            peer={peer}
            chatHist={chatHist}
            setChatHist={setChatHist}
            getChatHist={getChatHist}
          />
        )}
      </div>

      <Modal
        show={view === "report"}
        onHide={handleClose}
        centered
        className="report-popup"
      >
        <Modal.Header closeButton> </Modal.Header>
        <Modal.Body>
          <ReportUserPopup peer={peer} idToken={idToken} />
        </Modal.Body>
      </Modal>
      <Modal
        show={view === "block"}
        onHide={handleClose}
        centered
        className="report-popup"
      >
        <Modal.Header closeButton> </Modal.Header>
        <Modal.Body>
          <BlockUserPopUp
            handleClose={handleClose}
            nickname={peername}
            handleBlock={handleBlock}
          />
        </Modal.Body>
      </Modal>
    </div>
  );
};
export default Chat;

const ChatUserList = ({ last_unread, getChatHist }) => {
  const [filterUsers, setFilterUser] = useState(last_unread.last);
  const [count, setCount] = useState(last_unread.urCount);
  const [uPeers, setUPeers] = useState(last_unread.uPeers);

  useEffect(() => {
    setFilterUser(last_unread.last);
    setCount(last_unread.urCount);
    setUPeers(last_unread.uPeers);
  }, [last_unread]);
  const handleSearch = (event) => {
    let value = event.target.value.toLowerCase();
    if (value.length > 0)
      setFilterUser(
        filterUsers.filter(
          (peer) => peer.from.name.toLowerCase().search(value) !== -1
        )
      );
    else setFilterUser(last_unread.last);
  };
  return (
    <div className="chat-content">
      <div className="chat-search">
        <Form inline>
          <Form.Control
            type="text"
            onChange={(event) => handleSearch(event)}
            placeholder="Search User"
          />
          <Button>
            <i className="fa fa-search" />
          </Button>
        </Form>
      </div>

      <div className="chat-users">
        {filterUsers.map((peer, i) => {
          let status = uPeers.find(
            (el) => el.id === peer?.from?.uid
          )?.activeStatus;
          return (
            <div
              key={`item-${i}`}
              className={`chat-list ${!peer.isRead ? "unread-msg" : ""}`}
              onClick={() => {
                getChatHist(peer.from.uid, peer.photoURL, peer.from.name);
              }}
              role="presentation"
            >
              <div className="chat-user-pic">
                <PeerImage img={peer.photoURL} />
                {peer.from.uid === "777-our-VEGAS-BOT-uid-777" ? (
                  ""
                ) : (
                  <div className={`status-dot ${status}`}></div>
                )}
              </div>
              <div className="chat-user-content">
                <h4>
                  {peer.from.name}
                  <span>{peer.date.toDate().toDateString()}</span>
                </h4>
                <p
                  contentEditable="false"
                  dangerouslySetInnerHTML={{ __html: peer.body }}
                ></p>
              </div>
              {count[i] > 0 ? (
                <span className="unread-alert">{count[i]}</span>
              ) : (
                ""
              )}
            </div>
          );
        })}
      </div>
    </div>
  );
};

const SingleChatUser = ({
  usid,
  nick,
  userPhoto,
  peer,
  chatHist,
  setChatHist,
  getChatHist,
}) => {
  const dummy = useRef();
  const [emoji, setEmoji] = useState(null);
  const [message, setMessage] = useState("");
  const db = firebase.firestore();
  const [altDate, setAltDate] = useState(false);
  const onEmojiClick = (e, emojiUrl) => {
    let msg = `<img src='${emojiUrl}' alt='LV-emoji' />`;
    sendMessage(e, msg);
    setEmoji(false);
  };

  const toggleEmoji = () => {
    setEmoji(!emoji);
  };

  useEffect(() => {
    function scrollToBottom() {
      if (chatHist.length > 1) {
        dummy?.current?.scrollIntoView({ behavior: "auto" });
      }
    }
    if (chatHist.length > 1) {
      scrollToBottom();
    }
  }, [chatHist]);

  useEffect(() => {
    const db = firebase.firestore();
    db.collection("messanger")
      .doc(usid)
      .collection("chats")
      .where("from.uid", "in", [usid, peer])
      .where("to", "==", usid)
      .orderBy("date", "asc")
      .limitToLast(500)
      .onSnapshot((snapshot) => {
        let changes = snapshot.docChanges();

        setChatHist((preState) => {
          let oldState = [...preState];
          changes.forEach((change) => {
            if (change.type === "added") oldState.push(change.doc.data());
          });
          return oldState;
        });
      });

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  function getMessage(event) {
    setMessage(event.target.value);
  }

  async function sendMessage(event, msg) {
    event.preventDefault();
    let msgData = {
      from: { name: nick, uid: usid },
      to: peer,
      body: msg ? msg : message,
      date: firebase.firestore.FieldValue.serverTimestamp(),
      photoURL: userPhoto,
      isRead: false,
    };
    setMessage("");
    // resetting form value and scrolling to bottom
    let arr = chatHist;
    arr.push(msgData);
    setAltDate(true);
    setChatHist(arr);
    await db
      .collection("messanger")
      .doc(peer)
      .collection("chats")
      .doc()
      .set(msgData);
    msgData.isRead = true;
    await db
      .collection("messanger")
      .doc(usid)
      .collection("chats")
      .doc()
      .set(msgData);
    await axios.get("https://send-fcm-noti-t3e66zpola-uc.a.run.app/single", {
      headers: { cAction: `${window.location.origin}/profile` },
      params: {
        usid: peer,
        title: "Las Vegas Chat Messanger",
        body: msg ? `${nick} sent you a emoji` : `${nick}: "${message}"`,
      },
    });
    msgData.isRead = false;
    dummy?.current?.scrollIntoView({ behavior: "auto" });
  }

  return (
    <div className="chat-content">
      <div className="chat-users chat-message-box">
        {chatHist.map((msg, i) => {
          let side = "msg right-msg";
          let myDate = new Date().toLocaleString();
          let content = msg.body;
          console.log(content, 'gggggggggggggggggggggg')
          let dt;
          if (content) {
            let cc = content.split("at ");
            if (cc[1]) {
              let dd = cc[1].split(". ");
              cc.splice(1, 1, ...dd);

              dt = new Date(cc[1] + "Z").toLocaleString();
              cc[1] = dt + ". ";
              content = cc[0] + cc[1] + cc[2];
            }
			if(content.includes('wants to be your friend')){
				console.log("mes", content)
				let msgs = content.split(" ");
				let uname = msgs.splice(0,1);

				let remainBody = " " + msgs.join(" ");

				content = `<a href="${window.location.origin}/userprofile/${uname}">${uname}</a>${remainBody}`
			}
			if(content.includes('we are friends again!')){
				console.log("mes", content)
				let msgs = content.split(",");
				let uname = msgs.splice(0,1);

				let remainBody = " " + msgs.join(" ");
  
				content = `<a href="${window.location.origin}/userprofile/${uname}">${uname}</a>${remainBody}`
			}
			if(content.includes('we are friends now!')){
				console.log("mes", content)
				let msgs = content.split(",");
				let uname = msgs.splice(0,1);

				let remainBody = " " + msgs.join(" ");
  
				content = `<a href="${window.location.origin}/userprofile/${uname}">${uname}</a>${remainBody}`
			}
          }

          if (msg.from.uid === peer) {
            side = "msg left-msg";
          }
          if (!altDate) {
            myDate = msg.date.toDate().toLocaleString();
          }
          return (
            <div className={side} key={`item-${i}`}>
              <div className="msg-img">
                {/*<img src={convUri(msg.photoURL)} alt="user" />*/}
                <a href={`${window.location.origin}/userprofile/${msg.from.name}`}><PeerImage img={msg.photoURL} /></a>
              </div>
              <div className="msg-bubble">
                <div className="msg-info">
                  <div className="msg-info-name">
					<a href={`${window.location.origin}/userprofile/${msg.from.name}`}>{msg.from.name}</a>
				  </div>
                  <div className="msg-info-time">{myDate}</div>
                </div>
                {/* <div className="msg-text">{msg.body}</div> */}
                <div
                  contentEditable="false"
                  className="msg-text"
                  dangerouslySetInnerHTML={{ __html: content }}
                ></div>
                {msg.button && (
                  <ActionButton
                    type={msg.button}
                    peer={peer}
                    link={msg.link}
                    body={msg.body}
                    getChatHist={getChatHist}
                  />
                )}
              </div>
              <span ref={dummy}></span>
            </div>
          );
        })}
      </div>

      <div className="chat-search chat-input">
        <Form inline onSubmit={sendMessage}>
          <Form.Control
            type="text"
            placeholder="Say it now"
            value={message}
            onChange={getMessage}
          />
          <Button
            variant="outline-secondary"
            id="button-addon2"
            onClick={() => toggleEmoji()}
            className="emoji-btn"
          >
            <i className="fa fa-smile-o" aria-hidden="true"></i>
          </Button>
          <Button type="submit" disabled={message.length === 0}>
            <i className="fa fa-location-arrow" />
          </Button>
        </Form>
      </div>
      {emoji ? (
        <div className="emoji">
          <EmojiPicker onEmojiClick={onEmojiClick} />
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

const ActionButton = ({ type, peer, link, body, getChatHist }) => {
  const { setFriendList, setBlockList } = useContext(MainContext);
  const history = useHistory();
  const [usid] = useState(firebase.auth().currentUser.uid);
  const [idToken] = useState(firebase.auth().currentUser.getIdToken(true));
  const [isClicked, setIsClicked] = useState("");
  // const [coins, setCoins] = useState(0);

  useEffect(() => {
    const dd = async () => {
      // const us = firebase.auth().currentUser;
      // const baseStats = await getDoc("baseStats", us.uid);
      // setCoins(baseStats.total.coins);
    };
    dd();
  }, []);
  const handleAcceptRequest = async () => {
    setIsClicked(true);
    const nick = body.split(" ");
    toast.success("Sending..", {
      id: "please-wait",
      icon: "",
      style: {
        borderRadius: "5px",
        background: "#333",
        color: "#fff",
      },
    });
    axios
      .get(" https://accept-friend-t3e66zpola-ew.a.run.app", {
        params: { usid: usid, frId: nick[0] },
        headers: { idtoken: idToken.i },
      })
      .then((response) => {
        if (response.data) {
          let fName = nick[0];
          if (
            response.data.error === "no error" &&
            response.data.success === true
          ) {
            toast.success(
              "Congratulations! You and @" + fName + " are friends!",
              {
                duration: 3000,
                id: "frined-request",
                icon: "",
                style: {
                  borderRadius: "5px",
                  background: "#333",
                  color: "#fff",
                },
              }
            );
            axios
              .get("https://base-api-t3e66zpola-uk.a.run.app", {
                params: {
                  usid: usid,
                  service: "getFr-BlockTables",
                  params: "usid=" + usid,
                },
                headers: { idtoken: idToken },
              })
              .then((response) => {
                if (response.data) {
                  setFriendList(response.data.frTable);
                  setBlockList(response.data.blockTable);
                } else {
                  console.log("backend response failed: ", response.statusText);
                }
              })
              .catch((error) => {
                console.log("Error req", error);
              });
          }
          if (response.data.error.includes("already friends")) {
            toast.success("Friend request already accepted.", {
              duration: 6000,
              id: "frined-already-sent",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });
          } else if (response.data.error !== "no error") {
            toast.error(response.data.error, { id: "A" });
          }
        } else {
          console.log("Backend response failed: ", response.statusText);
        }
      })
      .catch((error) => {
        console.log("Error req", error);
      });
  };

  const handleCancelRequest = async () => {
    setIsClicked(true);
    toast.success("Sending..", {
      id: "please-wait",
      icon: "",
      style: {
        borderRadius: "5px",
        background: "#333",
        color: "#fff",
      },
    });

    const nick = body.split(" ");
    axios
      .get("https://reject-friend-req-t3e66zpola-lz.a.run.app", {
        params: { usid: usid, frId: nick[0] },
        headers: { idtoken: idToken.i },
      })
      .then((response) => {
        if (response.data) {
          if (
            response.data.error === "no error" &&
            response.data.success === true
          ) {
            toast.success("You have rejected this friend invitation!", {
              id: "frined-reject",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });
          }
        } else {
          console.log("Backend response failed: ", response.statusText);
        }
      })
      .catch((error) => {
        console.log("Error req", error);
      });
  };

  const handleSendRequest = async () => {
    setIsClicked(true);
    const FUid = peer;
    const IdTokenConst = idToken;
    const Uid = usid;
    axios
      .get("https://friend-reqest-t3e66zpola-uc.a.run.app", {
        params: { usid: Uid, frId: FUid },
        headers: { idtoken: IdTokenConst },
      })
      .then((response) => {
        if (response.data) {
          getChatHist();
        } else {
          console.log("Backend response failed: ", response.statusText);
        }
      })
      .catch((error) => {
        console.log("Error req", error);
      });
  };

  const handlePlayQuickMatch = async () => {
    const IdTokenConst = idToken;
    toast.success("Searching for a Poker match..", {
      id: "Searching-poker",
      icon: "",
      style: {
        borderRadius: "5px",
        background: "#333",
        color: "#fff",
      },
    });
    axios
      .get("https://quick-match-t3e66zpola-uk.a.run.app", {
        params: {
          uid: usid,
          gameColl: "pokerCP_Tables",
        },
        headers: {
          idtoken: IdTokenConst.i,
          device: localStorage.getItem("deviceId"),
        },
      })
      .then((response) => {
        if (response.data.error === "user logged in other device") {
          return (window.location.href = "/login");
        }
        if (response.data) {
          if (
            response.data.error === "no error" &&
            response.data.success === true
          ) {
            let pokerQuickLink = response.data.link;
            toast.success("Redirect..", {
              id: "quick-match-poker",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });
            window.location.href = pokerQuickLink;
          }
          if (response.data.error === "sorry, no table found") {
            toast.success("No Poker table found", {
              id: "error-login",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            });
          }
          if (response.data.error === "already inGame") {
            toast.success(
              "You are already inGame, please leave table and try again.",
              {
                duration: 5000,
                id: "error-inGame",
                icon: "",
                style: {
                  borderRadius: "5px",
                  background: "#333",
                  color: "#fff",
                },
              }
            );
          }
        } else {
          console.log(
            "quick-match backend response failed: ",
            response.statusText
          );
        }
      })
      .catch((error) => {
        console.log("Error req", error);
      });
  };

  const handleJoinTable = async () => {
    let d = link.split("=");
    let tabId = d[1].split("&")[0];
    let gameColl = d[2];
    const res = await axios.get("https://set-ingame-t3e66zpola-uc.a.run.app/", {
      headers: {
        Idtoken: idToken.i,
        device: localStorage.getItem("deviceId"),
      },
      params: {
        gameColl,
        tabId,
      },
    });
    if (res.data.success) {
      const link1 = link.split("https://");
      window.location.href = `https://${link1[1]}`;
    } else if (res.data.error === "User is already lobby") {
      let pokerTableLink = res.data.link;
      window.location.href = pokerTableLink;
    } else if (res.data.error === "User has no session. Force signin") {
      toast.success("User has no session, force sign-out", {
        duration: 5000,
        id: "force-signout",
        icon: "",
        style: {
          borderRadius: "5px",
          background: "#333",
          color: "#fff",
        },
      });
      axios
        .get("https://base-api-t3e66zpola-uk.a.run.app/", {
          params: {
            usid: usid,
            service: "signout",
            params: `usid=${usid}`,
          },
          headers: { idtoken: idToken },
        })
        .then((response) => {
          if (response.data.error === "no error") {
            localStorage.removeItem("xtkn");
            firebase
              .auth()
              .signOut()
              .then(() => {
                // this.setState({ renderSignout: true });
                history.push("/login");
              })
              .catch((error) => {
                console.log(error);
              });
          } else {
            console.log("req failed: ", response.data);
          }
        })
        .catch((error) => {
          console.log("Error req", error);
        });
    } else if (res.data.error === "user logged in other device") {
      toast.success("Your account is currently logged into another device", {
        duration: 5000,
        id: "another-device",
        icon: "",
        style: {
          borderRadius: "5px",
          background: "#333",
          color: "#fff",
        },
      });
    } else {
      toast.success("Could not find game table.", {
        duration: 5000,
        id: "find-game",
        icon: "",
        style: {
          borderRadius: "5px",
          background: "#333",
          color: "#fff",
        },
      });
    }
  };

  const handleBetAccept = async () => {
    let url = link.split("/");
    history.push("/" + url[url.length - 1]);
  };

  switch (type) {
    case "send-friend-request": {
      return <Button onClick={() => handleSendRequest()}>Send</Button>;
    }
    case "play-quick-match": {
      return <Button onClick={() => handlePlayQuickMatch()}>Play</Button>;
    }
    case "join-table": {
      return <Button onClick={() => handleJoinTable()}>Join</Button>;
    }
    case "accept-friend-request": {
      return (
        <>
          {!isClicked && (
            <>
              <Button onClick={() => handleAcceptRequest()}>Accept</Button>
              <Button onClick={() => handleCancelRequest()}>Reject</Button>
            </>
          )}
        </>
      );
    }
    case "open-bet-form": {
      return (
        <>
          {!isClicked && (
            <>
              <Button onClick={() => handleBetAccept()}>Bet Details</Button>
            </>
          )}
        </>
      );
    }
    default: {
      return "";
    }
  }
};

const PeerImage = ({ img }) => {
  return <img src={img} alt="Peer-Profile" />;
};
