import React, { useState } from "react";
import { Button, Form, Spinner } from "react-bootstrap";
// import Layout from "../layout/layout";
// import logo from "../../assets/images/header/Final-Logo-PNG-text.png";
import toast from "react-hot-toast";
import { Link } from "react-router-dom";
import firebase from "../../firebase";
import axios from "axios";
import { useEffect } from "react";

const FormGmailUserNickName = (props) => {
  const [loading, setLoading] = useState(false);
  const [nick, setNick] = useState("");
  const [message, setMessage] = useState("");
  const [checked, setChecked] = useState(false);
  const regexAlphabets =  /^[a-zA-Z_]+$/;

  useEffect(()=>{
	const geolocationSuccess = async(position) => {
		const {
		  coords: { latitude, longitude },
		} = position;
		console.log("Dsdsd =>", position);
		const res = await axios.get(
		  `http://api.geonames.org/countryCodeJSON?lat=${latitude}&lng=${longitude}&username=rizwanwebsul`
		);
		console.log("res =>", res.data);
		this.setState({ countryCode: res.data.countryCode });
		this.handleUserOrigin(this.state.ip, res.data.countryCode);
	  }
	  const getCountryCode = async() => {
	  let countryCode;
    const url =
      "https://geolocation-db.com/json/0f761a30-fe14-11e9-b59f-e53803842572";
    const response = await axios.get(url);
    let { data } = response;
    if (data.country_code === "Not found" || !data?.country_code) {
      const res = await axios.get("https://www.cloudflare.com/cdn-cgi/trace");
      const values = res.data.split("\n");
      let obj = {};
      values.forEach((value) => {
        obj[value.split("=")[0]] = value.split("=")[1];
      });
      data = { IPv4: obj.ip, country_code: obj.loc };
    }
    props.handleUserOrigin(data.IPv4, data.country_code);
    countryCode = data.country_code;

    // SetUp phone Country code flag according to Geolocation data.country_name IP
    if (
      countryCode === "International" ||
      countryCode === "Not found" ||
      countryCode === "" ||
      !countryCode
    ) {
      if (window.navigator) {
        window.navigator.geolocation.getCurrentPosition(
          async (position) => {
            countryCode = await geolocationSuccess(position);
          },
          (err) => {
            console.log("error in geolocation", err);
          },
          {
            enableHighAccuracy: true,
            timeout: 5000,
            maximumAge: 0,
          }
        );
      }
    }

}
getCountryCode()
  },[])
  const handleChange = (event) => {
	
    if (event.target.value === '' || regexAlphabets.test(event.target.value)) {
		setNick(event.target.value);
    }else{
      setMessage( 'Nick name should contains only alphabets and _' );
    }
    
  };

  const nickValidate = () => {
    let nickError = "";
    if (!isNaN(nick.charAt(0))) {
      nickError = "Nick name first letter cannot be empty or a number";
    }
    if (!regexAlphabets.test(nick)) {
      nickError = "'Nick name should contains only alphabets and _";
    }
    if (nickError) {
      setMessage(nickError);
      return false;
    }
    return true;
  };

  const termsnValidate = () => {
    let termsChecked = checked;
    let termsError = "You must agree to our terms and conditions";
    if (termsChecked === false) {
      setMessage(termsError);
      return false;
    }
    return true;
  };

  const handleRegisterClick = async (event) => {
    event.preventDefault();

    const nickisValid = nickValidate();
    const termsValid = termsnValidate();
    if (termsValid && nickisValid) {
      setLoading(true);
      const response = await props.handleGmailRegsiter({
        nick: nick,
        terms: checked,
      });
      setLoading(false);
      const myErrorVariable = response.data.error;
      if (myErrorVariable === "bad password") {
        setMessage(
          "Password must contain 8 characters with at least 1 uppercase"
        );
      }
      if (myErrorVariable === "nick must be alphanumerical") {
        toast.success(
          "Nick name must be alphanumerical without spaces, (-) score allowed",
          {
            duration: 6000,
            id: "nick-alphanumerical",
            icon: "",
            style: {
              borderRadius: "5px",
              background: "#333",
              color: "#fff",
            },
          }
        );
        setMessage(
          "Nick name must be alphanumerical without spaces, (-) score allowed"
        );
      }
      if (myErrorVariable === "nick exists") {
        toast.success("Sorry username already taken, please choose another", {
          id: "username-taken",
          duration: 3000,
          icon: "",
          style: {
            borderRadius: "5px",
            background: "#333",
            color: "#fff",
          },
        });
        setMessage("Sorry username already taken, please choose another");
      }
      if (myErrorVariable === "email belongs to active user") {
        toast.success("Email already in use, please choose another", {
          id: "email-taken",
          duration: 3000,
          icon: "",
          style: {
            borderRadius: "5px",
            background: "#333",
            color: "#fff",
          },
        });
        setMessage("Sorry email already taken, please choose another");
      }
      if (myErrorVariable === "registration denied. Phone belongs to user") {
        toast.success("User exits with your Gmail phone number", {
          id: "email-taken",
          duration: 3000,
          icon: "",
          style: {
            borderRadius: "5px",
            background: "#333",
            color: "#fff",
          },
        });
        setMessage("Sorry email already taken, please choose another");
      }
      if (myErrorVariable === "terms not accepted") {
        toast.success("Please agree to the terms and conditions", {
          id: "error-login",
          duration: 4000,
          icon: "",
          style: {
            borderRadius: "5px",
            background: "#333",
            color: "#fff",
          },
        });
        setMessage("You must agree to the terms and conditions");
      }
      if (
        response.data.error === "no error" &&
        response.data.success === true
      ) {
        toast.success("Account created, Successfull login..", {
          duration: 6000,
          id: "account-success",
          icon: "",
          style: {
            borderRadius: "5px",
            background: "#333",
            color: "#fff",
          },
        });
        window.gtag("event", "new_register", {
          author: "Register",
          title: "New user registration to system",
        });
        setMessage("Account successfully created");

        firebase
          .auth()
          .signInWithCustomToken(response.data.idtoken)
          .then(async (userCredential) => {
            var user = userCredential.user;
            const idToken = await firebase.auth().currentUser.getIdToken(true);
            await axios.get(
              "https://push-token-init-t3e66zpola-uc.a.run.app/addUid",
              {
                headers: {
                  Idtoken: idToken,
                  fmctoken: localStorage.getItem("fmcToken"),
                },
              }
            );
            localStorage.setItem("xtkn", response.data.idtoken);
            localStorage.setItem("uid", user.uid);
            setMessage("Successfully login");
            props.gotoProfilePage()
          })
          .catch((error) => {
            console.log(error, "error");
            setMessage(error.message);
            // ...
          });
      } else {
        toast.success(myErrorVariable, {
          id: "backend-error",
          icon: "",
          style: {
            borderRadius: "5px",
            background: "#333",
            color: "#fff",
          },
        });
      }
    }
  };

  const handleChecked = (e) => {
    const { checked } = e.target;
    setChecked(checked);
  };

  return (
    <div className="profile signup account-create">
      <h1>One More Step</h1>
      <p1>Choose Your Nickname</p1>
      <div className="rigister-tab">
        <Form>
          <div className="step-3">
            <Form.Group controlId="formNickName">
              <Form.Label>Nick Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter your nickname"
                name="nickname"
                autoComplete="off"
                //defaultValue={nick}
				value={nick}
                onChange={handleChange}
                required
              />

              <p style={{ color: "#b9a11e" }}>{message}</p>
            </Form.Group>
            <div className="agreebox">
              <input
                type="checkbox"
                className="checkbox16"
                onChange={handleChecked}
                defaultChecked={checked}
              />
              <small>
                &nbsp; I am 21 years of age or older and agree to{" "}
                <Link
                  className="signup-link"
                  target="_blank"
                  to={"/terms-of-use"}
                >
                  Terms
                </Link>
                &nbsp; and &nbsp;
                <Link
                  className="signup-link"
                  target="_blank"
                  to={"/privacy-policy"}
                >
                  Privacy policy
                </Link>
              </small>
            </div>
            <div className="text-center">
              <Button
                className="s-btn"
                type="submit"
                disabled={loading}
                onClick={handleRegisterClick}
              >
                {loading ? <Spinner animation="border" /> : "Create Account"}
              </Button>
            </div>
          </div>
        </Form>
      </div>
    </div>
  );
};

export default FormGmailUserNickName;
