import React, { Component } from "react";
import axios from "axios";
import { Form, Button, Modal } from "react-bootstrap";
import { Link } from "react-router-dom";
import Lottie from "react-lottie";
import toast from "react-hot-toast";
// import Layout from "../layout/layout";
// import logo from "../../assets/images/register/logocoin.png";
import diceLoader from "../../assets/images/loader/DiceLVsmall.gif";
import loader from "../../assets/animation/loader";
import "./register.css";
import firebase from "../../firebase";

const loaderIcon = {
  loop: true,
  autoplay: true,
  animationData: loader,
};

export class FormAccount extends Component {
  constructor(props) {
    super(props);
    this.state = {
      phone: this.props.data.phone,
      nick: "",
      email: this.props.data.email || "",
      pw: "",
      checked: false,
      message: "",
      bonusCoins: this.props.bonusdata.bonusUid,
      loading: false,
    };

    this.handleNickName = this.handleNickName.bind(this);
    this.handleEmail = this.handleEmail.bind(this);
    this.handlePasswprd = this.handlePasswprd.bind(this);
    this.formCreateAccount = this.formCreateAccount.bind(this);
  }

  handleNickName(event) {
    const regexAlphabets = /^[a-zA-Z_]+$/;
    if (event.target.value === '' || regexAlphabets.test(event.target.value)) {
       this.setState({
         nick: event.target.value,
      });
    }else{
      this.setState({ message: 'Nick name should contains only alphabets and _' });
    }
    // this.setState({ nick: event.target.value });
  }

  handleEmail(event) {
    this.setState({ email: event.target.value });
  }

  handlePasswprd(event) {
    this.setState({ pw: event.target.value });
  }

  handleChange = (e) => {
    const { checked } = e.target;
    this.setState({
      checked: checked,
    });
  };

  validate = () => {
    let passError = "";
    let nickNameError = "";
    let emailError = "";

    if (!this.state.email.includes("@")) {
      emailError = "Invalid email";
    }
    if (this.state.nick === "") {
      nickNameError = "Please enter a Nick name";
    }
    if (this.state.pw === "") {
      passError = "Please enter a password";
    }
    if (emailError) {
      this.setState({ message: emailError });
      return false;
    }
    if (passError) {
      this.setState({ message: passError });
      return false;
    }
    if (nickNameError) {
      this.setState({ message: nickNameError });
      return false;
    }
    return true;
  };

  nickValidate = () => {
    let nickVal = this.state.nick;
    let nickError = "";
    const regexAlphabets = /^[a-zA-Z_]+$/;
    if (!isNaN(nickVal.charAt(0))) {
      nickError = "Nick name first letter cannot be empty or a number";
    }
    if (
      nickVal.includes(
        "!",
        "@",
        "~",
        "`",
        "#",
        "$",
        "%",
        "^",
        "&",
        "*",
        "(",
        ")",
        "+",
        "=",
        "/",
        "|",
        "{",
        "}",
        ".",
      )
    ) {
      nickError = "Nick name special characters are not allowed";
    }else if(!regexAlphabets.test(nickVal)){
      nickError = 'Nick name should contains only alphabets and ';
    }
    if (nickError) {
      this.setState({ message: nickError });
      return false;
    }
    return true;
  };

  termsnValidate = () => {
    let termsChecked = this.state.checked;
    let termsError = "You must agree to our terms and conditions";
    if (termsChecked === false) {
      this.setState({ message: termsError });
      return false;
    }
    return true;
  };

  formCreateAccount(event) {
    event?.preventDefault();
    const isValid = this.validate();
    const nickisValid = this.nickValidate();
    const termsValid = this.termsnValidate();
    if (termsValid) {
      if (isValid && nickisValid) {
        this.setState({ loading: true });
        axios
          .get("https://init-reg-t3e66zpola-ez.a.run.app", {
            params: {
              service: "createUser",
              phone: this.state.phone,
              nick: this.state.nick,
              email: this.state.email,
              terms: this.state.checked,
              isMobile: this.props.data.isMobile ? "yes" : "no",
            },
            headers: {
              adsid: this.state.bonusCoins,
              pw: this.state.pw,
              ip: this.props.data.ip,
              device: this.props.data.deviceId,
            },
          })
          .then((response) => {
            this.setState({ loading: false });
            if (response.data) {
              const myErrorVariable = response.data.error;
              if (myErrorVariable === "bad password") {
                this.setState({
                  message:
                    "Password must contain 8 characters with at least 1 uppercase",
                });
              }
              if (myErrorVariable === "nick must be alphanumerical") {
                toast.success(
                  "Nick name must be alphanumerical without spaces, (-) score allowed",
                  {
                    duration: 6000,
                    id: "nick-alphanumerical",
                    icon: "",
                    style: {
                      borderRadius: "5px",
                      background: "#333",
                      color: "#fff",
                    },
                  }
                );
                this.setState({
                  message:
                    "Nick name must be alphanumerical without spaces, (-) score allowed",
                });
              }
              if (myErrorVariable === "nick exists") {
                toast.success(
                  "Sorry username already taken, please choose another",
                  {
                    id: "username-taken",
                    duration: 3000,
                    icon: "",
                    style: {
                      borderRadius: "5px",
                      background: "#333",
                      color: "#fff",
                    },
                  }
                );
                this.setState({
                  message:
                    "Sorry username already taken, please choose another",
                });
              }
              if (myErrorVariable === "this nick is forbidden") {
                toast.success("Special characters not allowed in nickname", {
                  id: "nickname-not-allowed",
                  duration: 3000,
                  icon: "",
                  style: {
                    borderRadius: "5px",
                    background: "#333",
                    color: "#fff",
                  },
                });
                this.setState({
                  message: "Special characters not allowed in nickname",
                });
              }
              if (myErrorVariable === "nick too long") {
                toast.success("nickName is too long, please change it", {
                  id: "nickname-long",
                  duration: 3000,
                  icon: "",
                  style: {
                    borderRadius: "5px",
                    background: "#333",
                    color: "#fff",
                  },
                });
                this.setState({
                  message: "nickName is too long, please change it",
                });
              }
              if (myErrorVariable === "nick too short") {
                toast.success("nickName is nick too short, please change it", {
                  id: "nickname-short",
                  duration: 3000,
                  icon: "",
                  style: {
                    borderRadius: "5px",
                    background: "#333",
                    color: "#fff",
                  },
                });
                this.setState({
                  message: "nickName is too long, please change it",
                });
              }
              if (myErrorVariable === "email exists") {
                toast.success(
                  "Email already exists, please choose a different email",
                  {
                    id: "email-taken",
                    duration: 3000,
                    icon: "",
                    style: {
                      borderRadius: "5px",
                      background: "#333",
                      color: "#fff",
                    },
                  }
                );
                this.setState({
                  message:
                    "Email already exists. please choose a different email",
                });
              }
              if (
                myErrorVariable ===
                "The user with the provided email already exists (EMAIL_EXISTS)."
              ) {
                toast.success(
                  "Email already exists, please choose a different email",
                  {
                    id: "email-taken2",
                    duration: 3000,
                    icon: "",
                    style: {
                      borderRadius: "5px",
                      background: "#333",
                      color: "#fff",
                    },
                  }
                );
                this.setState({
                  message:
                    "Email already exists. please choose a different email",
                });
              }
              if (myErrorVariable === "terms not accepted") {
                toast.success("Please agree to the terms and conditions", {
                  id: "error-login",
                  duration: 4000,
                  icon: "",
                  style: {
                    borderRadius: "5px",
                    background: "#333",
                    color: "#fff",
                  },
                });
                this.setState({
                  message: "You must agree to the terms and conditions",
                });
              }
              if (
                response.data.error === "no error" &&
                response.data.success === true
              ) {
                toast.success(
                  "Account created, Verification link sent to your email",
                  {
                    duration: 6000,
                    id: "account-success",
                    icon: "",
                    style: {
                      borderRadius: "5px",
                      background: "#333",
                      color: "#fff",
                    },
                  }
                );

                window.gtag("event", "new_register", {
                  author: "Register",
                  title: "New user registration to system",
                });
                this.setState({
                  message: "Account successfully created",
                });
                firebase
                  .auth()
                  .signInWithCustomToken(response.data.idtoken)
                  .then(async (userCredential) => {
                    var user = userCredential.user;
                    const idToken = await firebase
                      .auth()
                      .currentUser.getIdToken(true);
                    localStorage.setItem("xtkn", response.data.idtoken);
                    localStorage.setItem("uid", user.uid);
                    await axios.get(
                      "https://push-token-init-t3e66zpola-uc.a.run.app/addUid",
                      {
                        headers: {
                          Idtoken: idToken,
                          fmctoken: localStorage.getItem("fmcToken"),
                        },
                      }
                    );
                    this.props.gotoProfilePage();
                  })
                  .catch((error) => {
                    console.log(error, "error");
                    this.setState({
                      message: error.message,
                    });
                    // ...
                  });
              } else {
                toast.success(myErrorVariable, {
                  id: "backend-error",
                  icon: "",
                  style: {
                    borderRadius: "5px",
                    background: "#333",
                    color: "#fff",
                  },
                });
              }
            }
            this.setState({
              data: response.data,
            });
          })
          .catch((error) => {
            this.setState({ loading: false });
            console.log("Error fetching and parsing data", error);
          });
      }
    }
  }

  handleRegisterClick = async (event) => {
    event.preventDefault();
    if (this.props.data.isGoogle) {
      const isValid = this.validate();
      const nickisValid = this.nickValidate();
      const termsValid = this.termsnValidate();

      if (termsValid && isValid && nickisValid) {
        this.setState({ loading: true });
        const response = await this.props.handleGmailRegsiter({
          nick: this.state.nick,
          password: this.state.pw,
          terms: this.state.checked,
        });
        this.setState({ loading: false });
        const myErrorVariable = response.data.error;
        if (myErrorVariable === "bad password") {
          this.setState({
            message:
              "Password must contain 8 characters with at least 1 uppercase",
          });
        }
        if (myErrorVariable === "nick must be alphanumerical") {
          toast.success(
            "Nick name must be alphanumerical without spaces, (-) only score allowed",
            {
              duration: 6000,
              id: "nick-alphanumerical",
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            }
          );
          this.setState({
            message:
              "Nick name must be alphanumerical without spaces, only (-) score allowed",
          });
        }
        if (myErrorVariable === "nick exists") {
          toast.success("Sorry username already taken, please choose another", {
            id: "username-taken",
            duration: 3000,
            icon: "",
            style: {
              borderRadius: "5px",
              background: "#333",
              color: "#fff",
            },
          });
          this.setState({
            message: "Sorry username already taken, please choose another",
          });
        }
        if (myErrorVariable === "email exists") {
          toast.success(
            "Email already exists, please choose a different email",
            {
              id: "email-taken",
              duration: 3000,
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            }
          );
          this.setState({
            message: "Email already exists. please choose a different email",
          });
        }
        if (
          myErrorVariable ===
          "The user with the provided email already exists (EMAIL_EXISTS)."
        ) {
          toast.success(
            "Email already exists, please choose a different email",
            {
              id: "email-taken2",
              duration: 3000,
              icon: "",
              style: {
                borderRadius: "5px",
                background: "#333",
                color: "#fff",
              },
            }
          );
          this.setState({
            message: "Email already exists. please choose a different email",
          });
        }
        if (myErrorVariable === "terms not accepted") {
          toast.success("Please agree to the terms and conditions", {
            id: "error-login",
            duration: 4000,
            icon: "",
            style: {
              borderRadius: "5px",
              background: "#333",
              color: "#fff",
            },
          });
          this.setState({
            message: "You must agree to the terms and conditions",
          });
        }
        if (
          response.data.error === "no error" &&
          response.data.success === true
        ) {
          toast.success("Account created, please login..", {
            duration: 6000,
            id: "account-success",
            icon: "",
            style: {
              borderRadius: "5px",
              background: "#333",
              color: "#fff",
            },
          });
          window.gtag("event", "new_register", {
            author: "Register",
            title: "New user registration to system",
          });
          this.setState({
            message: "Account successfully created",
          });
          this.props.goToLogin();
        }
      }
    } else {
      this.formCreateAccount(event);
    }
  };

  render() {
    return (
      <div className="profile signup account-create">
        <h1>One More Step</h1>
        <p1>Complete registration data</p1>
        <div className="rigister-tab">
          <Form>
            <div className="step-3">
              <div className="row">
                <div className="col-md-6">
                  <Form.Group controlId="formNickName">
                    <Form.Label>Nick Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter your nickname"
                      name="nickname"
                      autoComplete="off"
                      value={this.state.nick}
                      onChange={this.handleNickName}
                      required
                    />
                  </Form.Group>
                </div>
                <div className="col-md-6">
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="email"
                      placeholder="Enter your email"
                      name="email"
                      autoComplete="off"
                      disabled={this.props.data.isGoogle}
                      value={this.state.email}
                      onChange={this.handleEmail}
                      required
                    />
                  </Form.Group>
                </div>
                <div className="col-md-12 password-des">
                  <Form.Group controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      type="password"
                      placeholder="Enter your password"
                      name="password"
                      autoComplete="off"
                      value={this.state.value}
                      onChange={this.handlePasswprd}
                      required
                    />
                    <p style={{ color: "#b9a11e" }}>{this.state.message}</p>
                    <p style={{ color: "#b9a11e" }}>{this.state.emailError}</p>
                  </Form.Group>
                </div>

                <div className="agreebox col-md-12">
                  <input
                    type="checkbox"
                    className="checkbox16 form-check-input"
                    onChange={(e) => this.handleChange(e)}
                    defaultChecked={this.state.checked}
                  />
                  <small>
                    &nbsp; I am 21 years of age or older and agree to{" "}
                    <Link
                      className="signup-link"
                      target="_blank"
                      to={"/terms-of-use"}
                    >
                      Terms
                    </Link>
                    &nbsp; and &nbsp;
                    <Link
                      className="signup-link"
                      target="_blank"
                      to={"/privacy-policy"}
                    >
                      Privacy policy
                    </Link>
                  </small>
                </div>

                <div className="col-md-12">
                  <div className="text-center">
                    <Button
                      className="s-btn"
                      type="submit"
                      onClick={this.handleRegisterClick}
                      disabled={this.state.loading}
                    >
                      {this.state.loading ? (
                        <Lottie options={loaderIcon} height={30} width={30} />
                      ) : (
                        "Create Account"
                      )}
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          </Form>
        </div>
        <Modal show={this.state.loading} centered className="loader-popup">
          <Modal.Body>
            <div className="loader-popup-content">
              <img src={diceLoader} alt="loader" />
              <h3>Account is creating...</h3>
            </div>
          </Modal.Body>
        </Modal>
      </div>
    );
  }
}

export default FormAccount;
