import React, { Component } from "react";
import FormPhone from "./formPhone";
import FormOTP from "./formOTP";
import FormAccount from "./formAccount";
import firebase from "../../firebase";
import {
  mobileVendor,
  // BrowserView,
  // MobileView,
  // isSafari,
  // isIOS,
  isMobile,
  browserName,
  mobileModel,
  getUA,
} from "react-device-detect";
import { Redirect, withRouter } from "react-router-dom";
import "./register.css";
import axios from "axios";
import FormGmailUserNickName from "./formGmailUserNickName";
import toast from "react-hot-toast";

class Register extends Component {
  constructor() {
    super();
    this.state = {
      step: 1,
      redirect: null,
      phone: "",
      bonusUid: "no",
      email: "",
      deviceId: "",
	  ip:"",
      isGoogle: false,
      accessToken: "",
      countryCode: "US",
      birthday: "",
      gender: "",
	  query: "",
    };
  }

  async componentDidMount() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.setState({
          redirect: "/profile",
        });
      }
    });
	const urlParams  = new URLSearchParams(window.location.search);
	if(urlParams.get('email')){
		this.setState({ isGoogle: true, email: urlParams.get("email"), step: 4});
	}
    var rx = new RegExp(/[?&]adsId=([^&]+).*$/);
    var returnVal = window.location.search.match(rx);
    if (returnVal == null) {
      this.setState({ bonusUid: "no" });
    } else {
      let bonusCoins = returnVal[1];
      this.setState({ bonusUid: bonusCoins });
    }
  }

  phoneUpdate = (phoneVar) => {
    this.setState({
      phone: phoneVar,
    });
  };

  emailUpdate = (value) => {
    this.setState({ email: value });
  };

  handleDeviceId = () => {
    const srt =
      "browserName_" +
      browserName +
      "_mobileVendor_" +
      mobileVendor +
      "_mobileModel_" +
      mobileModel +
      "_UA_" +
      getUA +
      this.state.ip;
    var device_str_id = srt.replace(/[^A-Z0-9]/gi, "_");
    this.setState({ deviceId: device_str_id });
    localStorage.setItem("deviceId", device_str_id);
  };

  handleGoogleLogin = async (email, accessToken, countryCode) => {
	toast.success("One moment please..", {
		id: "please-wait-login",
		icon: "",
		style: {
		  borderRadius: "5px",
		  background: "#333",
		  color: "#fff",
		},
	  });
	  const response = await axios.get(
		`https://login-google-mail-t3e66zpola-uc.a.run.app/`,
		{
		  headers: {
			email,
			ip: this.state.ip,
			deviceId: this.state.deviceId,
		  },
		  params: {
			isMobile: isMobile ? "yes" : "no",
		  },
		}
	  );
	  this.setState({ loading: "" });
	  if (response.data.error === "no error") {
		toast.success("Successfully Logged in", {
		  id: "Successfully-Logged",
		  icon: "",
		  style: {
			borderRadius: "5px",
			background: "#333",
			color: "#fff",
		  },
		});
		firebase
		  .auth()
		  .signInWithCustomToken(response.data.idtoken)
		  .then(async (userCredential) => {
			var user = userCredential.user;
			const idToken = await firebase.auth().currentUser.getIdToken(true);
			this.setState({
			  uid: user.uid,
			  idToken: response.data.idtoken,
			});
			localStorage.setItem("xtkn", response.data.idtoken);
			localStorage.setItem("uid", user.uid);
			await axios.get(
			  "https://push-token-init-t3e66zpola-uc.a.run.app/addUid",
			  {
				headers: {
				  Idtoken: idToken,
				  fmctoken: localStorage.getItem("fmcToken"),
				},
			  }
			);
			this.setState({ message: "Successfully login" });
			this.goToProfile();
			// ...
		  })
		  .catch((error) => {
			console.log(error, "error");
			this.setState({ message: error.message });
			// ...
		  });
	  }
	  if (response.data.error === "already logged-in and inGame") {
		this.setState({
		  message: "User already logged-in and inGame",
		});
		toast.success("User already login and playing a game at the moment", {
		  duration: 6000,
		  id: "loggedin-inGame",
		  icon: "",
		  style: {
			borderRadius: "5px",
			background: "#333",
			color: "#fff",
		  },
		});
	  } if (response.data.error === "no user with that email") {
	  
    window.gtag("event", "Google_registration_success", {
        title: "Google registration button event",
      });
    this.setState({ isGoogle: true, email, step: 4, accessToken, countryCode });
    const res = await axios.get(
      `https://people.googleapis.com/v1/people/me?personFields=phoneNumbers,birthdays,genders,locations&access_token=${accessToken}`
    );
    console.log("res =>", res.data, accessToken);
    const {
      data: { phoneNumbers, birthdays, genders },
    } = res;
    if (phoneNumbers?.length) {
      this.setState({ phone: phoneNumbers[0].canonicalForm });
    }
    if (birthdays?.length) {
      const { month, day } = birthdays[0].date;
      let dd = new Date();
      dd.setMonth(month);
      dd.setDate(day);
      this.setState({ birthday: dd.toISOString() });
    }
    if (genders?.length) {
      this.setState({ gender: genders[0].value });
    }

} else if (response.data.sucess === false) {
this.setState({ message: response.data.error });
toast.success(this.state.message, {
  id: "error-login",
  icon: "",
  style: {
	borderRadius: "5px",
	background: "#333",
	color: "#fff",
  },
});
}
  };

  handleUserOrigin = (ip, countryCode) => {
	this.setState({ ip, countryCode});
	this.handleDeviceId()
  }

  // Proceed to next step
  nextStep = (value) => {
    const { step } = this.state;
    this.setState({
      step: value ? value : step + 1,
    });
  };

  // Goto login
  goToLogin = () => {
    this.setState({ redirect: "/login" });
  };

  gotoProfilePage = () => {
    this.setState({ redirect: "/profile" });
    this.props.history.push({
      pathname: "/profile",
      state: { newRegister: true },
    });
  };

  handleGmailRegsiter = async (values) => {
    localStorage.removeItem("welcomePopup");
    const res = await axios.get(
      "https://create-user-google-t3e66zpola-uc.a.run.app/",
      {
        headers: {
          ip: this.state.ip,
          device: this.state.deviceId,
          fmctoken: localStorage.getItem("fmcToken")
            ? localStorage.getItem("fmcToken")
            : "no",
        },
        params: {
          email: this.state.email,
          isMobile: isMobile ? "yes" : "no",
          phone: this.state.phone || "no",
          nick: values.nick,
          terms: values.terms,
          countryCode: this.state.countryCode || 'US',
          birth: this.state.birthday,
          gender: this.state.gender,
        },
      }
    );
    console.log(res.data);
    return res;
  };

  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }

    const { step } = this.state;

    switch (step) {
      case 1:
        return (
          <FormPhone
            nextStep={this.nextStep}
            phoneUpdate={this.phoneUpdate}
            data={{ phone: this.state.phone }}
            handleGoogleLogin={this.handleGoogleLogin}
			handleUserOrigin={this.handleUserOrigin}
          />
        );

      case 2:
        return (
          <FormOTP
            nextStep={this.nextStep}
            phoneUpdate={this.phoneUpdate}
            data={{ phone: this.state.phone }}
          />
        );

      case 3:
        return (
          <FormAccount
            goToLogin={this.goToLogin}
            phoneUpdate={this.phoneUpdate}
            gotoProfilePage={this.gotoProfilePage}
            data={{
              phone: this.state.phone,
              email: this.state.email,
              isGoogle: this.state.isGoogle,
              ip: this.state.ip,
              isMobile: isMobile ? "yes" : "no",
              deviceId: this.state.deviceId,
            }}
            bonusdata={{ bonusUid: this.state.bonusUid }}
            handleGmailRegsiter={this.handleGmailRegsiter}
          />
        );

      case 4:
        return (
          <FormGmailUserNickName
            goToLogin={this.goToLogin}
		    	  gotoProfilePage={this.gotoProfilePage}
            data={{
              phone: this.state.phone,
              email: this.state.email,
              isGoogle: this.state.isGoogle,
            }}
            handleGmailRegsiter={this.handleGmailRegsiter}
			handleUserOrigin={this.handleUserOrigin}
          />
        );

      default:
      // do nothing
    }
  }
}

export default withRouter(Register);
